import React, { useEffect, useState } from 'react';
import { Input, Button, Row, Col } from 'antd';
import { Container, Typography } from '@material-ui/core';
import data from '../data/cj';

export default function Index() {
    const [lorem, setLorem] = useState('');
    const [qtd, setQtd] = useState('');

    function handleGenerate() {
        let loremArray = [];

        for (let i = 0; i < qtd; i++) {
            let random = data[Math.floor(Math.random() * data.length)];
            loremArray = [...loremArray, random];
        }

        const lorem = loremArray.join(' ');

        setLorem(lorem);
    }

    return (
        <Container>
            <Typography variant="h4" style={{ marginBottom: 15 }}>CJ-Ipsum</Typography>
            <Typography variant="h5" style={{ marginBottom: 15 }}>Quantidade de Frases?</Typography>
            <Row>
                <Col span={6}>
                    <Input maxlength="3" onChange={e => setQtd(e.target.value)} />
                </Col>
                <Col span={6}>
                    <Button onClick={() => handleGenerate()}>Gerar</Button>
                </Col>
            </Row>

            <Typography variant="body1" style={{ marginTop: 50, color: '#000', fontSize: 22, textAlign: 'justify' }}>
                {lorem}
            </Typography>
        </Container>
    );
}
